.Recipe{
    font-family: Arial, Helvetica, sans-serif;
    border-radius: 10px;
    box-shadow: 10px 10px 10px rgb(71, 71, 71);
    margin: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    background-image: linear-gradient(to top, #fff1eb 0%, #ace0f9 100%);
    align-items: center;
    width: 700px;
    margin-left: 500px;


}