.App {
  text-align: center;
  min-height: 100vh;
  background-image: linear-gradient(120deg, #fccb90 0%, #d57eeb 100%);
}

.search-form{
  min-height: 10vh;
  display:flex;
  justify-content: center;
  align-items: center;
}

.search-bar{
  width: 50%;
  border: none;
  padding: 10px;
  border-radius: 6px;
}
.search-button{
  background-color: #d57eeb;
  border-radius: 5px;
  border: none;
  padding: 10px 20px;
  color: white;
  margin-left: 10px;
}
.recipes{
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}